body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#00171F;font-family:"Fauna One",serif}h2,h3,h4,h5,h6{font-family:"Julius Sans One",sans-serif}

.movileContact{display:none}.home{width:100%;display:block;margin-top:4%}.name{text-align:center;color:#fff;padding:10px}.name h1{margin:0;font-size:550%;font-weight:lighter}.name h2{margin-top:-25px;margin-left:360px}.logo{width:100%;display:flex;justify-content:center;height:auto}.logo img{width:280px;height:auto}@media only screen and (max-width: 800px){.home{width:100%;display:flex;flex-direction:column-reverse}.movileContact{display:block}.movileContact .imgContainer{display:block;position:fixed;flex-direction:row;bottom:0px}.logo{margin-top:30%;margin-bottom:125px}.logo img{width:200px}.name{bottom:0;font-size:55%}.name h2{font-size:200%;bottom:1px;margin:0}}

.imgContainer{position:fixed;left:0;top:40%;width:45px;display:block;margin-left:15px}.imgContainer img{width:45px;height:45px;margin-bottom:10px}.imgContainer img:hover{border-radius:30px;-webkit-animation-duration:4s;animation-duration:4s;-webkit-animation-name:onHoverImg;animation-name:onHoverImg}@-webkit-keyframes onHoverImg{from{width:45px;height:45px}10%{width:55px;height:55px;margin-bottom:0;-webkit-transform:rotate(-360deg);transform:rotate(-360deg)}to{width:55px;height:55px;margin-bottom:0;-webkit-transform:rotate(-360deg);transform:rotate(-360deg)}}@keyframes onHoverImg{from{width:45px;height:45px}10%{width:55px;height:55px;margin-bottom:0;-webkit-transform:rotate(-360deg);transform:rotate(-360deg)}to{width:55px;height:55px;margin-bottom:0;-webkit-transform:rotate(-360deg);transform:rotate(-360deg)}}@media only screen and (max-width: 800px){.imgContainer{display:none}}

.aboutContainer{color:#fff;display:flex;flex-direction:column;align-items:center;margin-bottom:80px}.aboutContainer a{color:#fff}.img-container{width:80%;height:auto;border-bottom:2px solid #fff;border-top:2px solid #fff;padding-bottom:25px;padding-top:30px;margin-top:10px;display:flex;justify-content:center}.img-container img{width:75%}.content{width:80%;display:felx;flex-direction:column;align-items:flex-start}.content p{font-size:large}

.skillCard{border-bottom:2px solid #fff;width:30%;margin-left:1%;margin-right:1%;text-align:center;padding-top:50px;padding-bottom:10px}.skillCard img{height:170px;width:auto;margin-bottom:-13px}@media only screen and (max-width: 800px){.skillCard{width:100%}}

.skillsContainer{color:#fff;width:90%;display:flex;flex-wrap:wrap;margin-top:50px;margin-left:5%;margin-bottom:100px}

.projectCard{margin-left:7%;width:86%;height:206;color:#fff;display:flex;margin-top:60px;border-bottom:3px solid #fff;padding:10px;justify-content:space-around}.projectCard a{color:#fff;font-weight:bold;text-decoration:none;-webkit-text-decoration-line:underline;text-decoration-line:underline}.projectCard img{margin-top:10px;width:408px;height:186px;flex-grow:0;border:2px solid #003459}.cardContent{display:flex;flex-direction:column;flex-grow:3;padding-left:16px}.cardContent p{margin:-10px 0 0 0}.skills{display:flex;flex-wrap:wrap}.singleSkill{margin-right:15px;margin-bottom:10px;padding:4px;background-color:#003459;color:#00A8E8;border-radius:15%}@media only screen and (max-width: 800px){.projectCard{display:flex;flex-direction:column;width:75%}.projectCard img{width:100%;height:auto;margin:0}.projectCards{margin-bottom:100px}}

.header{background-color:transparent;color:#fff;display:flex;justify-content:center;position:fixed;width:100%;z-index:2;top:0;margin-top:-15px}.header a{text-decoration:none;font-size:larger;color:#fff;padding:15px 30px;text-align:center;display:block}.header ul{list-style:none;margin:0;padding:0;overflow:hidden}.header li{background-color:rgba(0,23,31,0.6);float:left}.header li:hover{-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-name:onHoverMenu;animation-name:onHoverMenu}.container{display:none}@-webkit-keyframes onHoverMenu{from{padding-top:0;border-bottom:0px solid #fff;background-color:rgba(0,23,31,0.6)}10%{padding-top:3%;background-color:#00171F}to{padding-top:3%;border-bottom:2px solid #fff;background-color:#00171F}}@keyframes onHoverMenu{from{padding-top:0;border-bottom:0px solid #fff;background-color:rgba(0,23,31,0.6)}10%{padding-top:3%;background-color:#00171F}to{padding-top:3%;border-bottom:2px solid #fff;background-color:#00171F}}@media only screen and (max-width: 600px){.header li{display:none;background-color:transparent}.container{cursor:pointer;top:0;display:block;background-color:rgba(0,23,31,0.8)}.bar1,.bar2,.bar3{width:35px;height:5px;background-color:#fff;margin:6px 0;-webkit-transition:0.4s;transition:0.4s}.change .bar1{-webkit-transform:rotate(-45deg) translate(-9px, 6px);transform:rotate(-45deg) translate(-9px, 6px)}.change .bar2{opacity:0}.change .bar3{-webkit-transform:rotate(45deg) translate(-8px, -8px);transform:rotate(45deg) translate(-8px, -8px)}.responsive{position:fixed;background-color:rgba(0,23,31,0.93);padding-top:35px}.responsive li{float:none;display:block;text-align:left}.icon{position:fixed;top:0}}

